.contact {
  padding: 20px 200px;
}

.sub-heading {
  font-size: 30px !important;
}

.contact-item {
  margin: 10px 0;
}
.gmail-sub-heading{
  font-size: 15px;
  font-family: "Sora", sans-serif;
  font-weight: 400;
  line-height: 26px;
}

.contact-link {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: inherit; 
}

.contact-icon {
  width: 30px;
  height: 30px;
  margin-right: 10px;
}

.linkedin-icon {
  width: 25px;
  height: 25px;
  margin-right: 10px;
}

.contact-link p {
  margin: 0;
}
